import React, { ReactElement, useCallback, useState, useReducer, useEffect, useMemo } from 'react';
import ChessCom, { loadChessComMatch } from '../chess/ChessCom';
import * as icons from '../assets/icons/';
import * as regexes from '../utils/regexes';
import { FaLink, FaSearch, FaDownload } from 'react-icons/fa';
import { PairingObject, Player, TournamentStatus, TournamentSettings } from '../interfaces';
import { GET } from '../utils/requests';
import styles from '../css/pairings.module.css';
import AutoComplete from '../components/AutoComplete';

export interface PairingsProps {
	id: string
	status: TournamentStatus
	playerDict: Map<string, Player>
	settings: TournamentSettings
}

interface minMatch {
	id: string
	startTime?: number
	endTime?: number
	players?: number
	finished: boolean
}

export default function Pairings(props: PairingsProps & {round: number}): ReactElement {

    const [pairings, setPairings] = useState([] as PairingObject[]);
    const [expanded, setExpanded] = useReducer((state: string[], action: string) => {
		let list = state.slice(0);
		if (state.includes(action)) list.splice(list.indexOf(action));
		else list.push(action);
		return list;
	}, []);
	const [loaded, hasLoaded] = useState(false);

    const [matchDict, setMatch] = useReducer((state: {[key: string]: minMatch}, action: minMatch) => {
        let res = Object.assign({}, state);
        res[action.id] = action;
        return res;
	}, {});
	
	useEffect(() => {
		hasLoaded(false);
		GET({
			url: '/tournament/' + props.id + '/getPairings',
			params: { round: props.round }
		})
			.then(setPairings)
			.then(() => hasLoaded(true))
			.catch(() => {})
	}, [setPairings, props.round, props.id])

    const populatematchDict = useCallback(async () => {
        for (let row of pairings) {
            let link = Object.values(row[2]).find(v => v);
            if (!link) continue;
            let matches = link.match(regexes.chessCom) as RegExpMatchArray;
            if (!matches) link = '';
            else link = 'https://www.chess.com/club/matches/live/' + matches[1];
			let id = Number(matches[1]);
            loadChessComMatch(id)
                .then((body) => setMatch({
					id: matches[1],
					startTime: body.start_time * 1000,
					endTime: body.end_time ? body.end_time * 1000 : undefined,
					finished: body.status === 'finished'
				}))
                .catch(async (e) => {
                    if (e.message.includes('not found')) {
                        GET({
                            url: '/tournament/' + props.id + '/scrapeChessComTeamPage',
                            params: {
                                url: link
                            }
                        }).then((meta) => {
							setMatch({
								id: matches[1],
								startTime: meta['Start_Time'] ? new Date(meta['Start Time']).valueOf() : undefined,
								endTime: meta['End_Time'] ? new Date(meta['End Time']).valueOf() : undefined,
								players: meta['Players per Team'],
								finished: false
							});
						});
                    }
                    else console.error(e);
                });
        }
	}, [pairings, setMatch, props.id]);
	
    useEffect(() => {
        populatematchDict();
	}, [populatematchDict]);

	const getDisplayName = useCallback((opp: Player): string => {
        if (!opp || !opp.id) return '\u200b';
		if (opp.id === 'bye') return 'BYE';
		let displayName: string;
		if (props.settings.competitors === 'individual') displayName = [opp.firstName, opp.lastName].join(' ');
		else displayName = opp.firstName;
		return displayName;
	}, [props.settings.competitors]);
	
	const [choices, setChoices] = useState([] as Player[]);
	const headers = ['White', 'White score', 'Black', 'Black score', 'Game link', 'Time'] as string[];
	const [csv, setCSV] = useState([] as any[][]);

	const rows = useMemo(() => {
		let _csv = [] as any[][];
		let r = pairings.map((row, i) => {
			let id = [row[0].trim(), row[1].trim()].join('.');
			let w = props.playerDict.get(row[0].trim());
			let b = props.playerDict.get(row[1].trim());

			let hidden = !!choices.length && !choices.some(c => c.id === row[0]) && !choices.some(c => c.id === row[1]);

			let link = Object.values(row[2]).find(v => v);
			let matches = link?.match(regexes.chessCom) as RegExpMatchArray;
			if (!matches) link = '';
			else link = 'https://www.chess.com/club/matches/live/' + matches[1];

			let meta = matchDict && matches && matchDict[matches[1]] ? matchDict[matches[1]] : {} as {[key: string]: any};
			let [startTime, endTime] = [meta.startTime || Infinity, meta.endTime || Infinity];
			let [hasStarted, hasFinished] = [Date.now() > startTime, Date.now() > endTime];
			let enforceBoards: number | undefined = matches && matchDict[matches[1]] ? matchDict[matches[1]].players : undefined;

			let data = [
				getDisplayName(w as Player),
				w?.histories[props.round]?.game ?? '',
				getDisplayName(b as Player),
				b?.histories[props.round]?.game ?? '',
				link.trim(),
				meta?.startTime  || meta?.endTime ? new Date(meta.startTime || meta.endTime).toString().slice(0, 21) : ''
			] as string[];
			_csv[i + 1] = data;
			return (
				<>
					<tr key={['row', i].join('.')} style={{
						display: hidden ? 'none' : undefined
					}}>
						<td id={w?.id} onClick={() => w ? window.location.hash = '#' + w.id : ''}><div className='playerName'>{data[0]}</div></td>
						<td className={styles.inner}>
							{link ? <a className='button' href={data[4] as string} target='_blank' rel='noopener noreferrer'>
								<FaLink />
								<div>chess.com</div>
							</a> : null}
						</td>     
						<td className={styles.inner}>
							<div className={['bold', styles.score, (meta.finished || hasFinished) ? styles.finished : ((!meta.started && w?.histories[props.round]?.game !== null) || hasStarted) ? styles.live : styles.pending].join(' ')}>
								{data[1]}
							</div>
						</td>
						<td>{data[5] || '-'}</td>
						<td className={styles.inner}>
							<div className={['bold', styles.score, (meta.finished || hasFinished) ? styles.finished : ((!meta.started && w?.histories[props.round]?.game !== null) || hasStarted) ? styles.live : styles.pending].join(' ')}>
								{data[3]}
							</div>
						</td>
						<td className={styles.inner}>
							{link ? <div className='button' onClick={() => setExpanded(id)}>
								{!meta.finished ? <>
									<img className='icon' src={icons.watch} alt={'Watch games'}/>
									<div>Watch live</div>
								</> : <div>Analysis</div>}
							</div> : null}
						</td>
						<td id={b?.id} onClick={() => b ? window.location.hash = '#' + b.id : ''}><div className='playerName'>{data[2]}</div></td>
					</tr>
					{link && expanded.includes(id) ? <tr>
						<td colSpan={100}>
							{row[2].chessComLink || row[2].chessComTeamLink ?
								row[2].chessComTeamLink || true ?
									<ChessCom
										matchURL={row[2].chessComTeamLink || row[2].chessComLink as string}
										enforceBoards={enforceBoards ? Number(enforceBoards) : 0}
										round={props.round}
									/> :
									null :
								null
							}
						</td>
					</tr> : null}
				</>
			)
		});
		setCSV(_csv);
		return r;
	}, [pairings, props.playerDict, getDisplayName, setExpanded, setCSV, expanded, choices, matchDict, props.round]);

	const handleDownload = useCallback(() => {
		const element = document.createElement('a')
		const fileContent = headers.join(',') + '\n' + csv.map(line => line.join(',')).join('\n');
		const fileFormat = 'csv'
		const file = new Blob([fileContent], {type: 'text/csv'});
		const tourName = (props.status.name.match(regexes.lowerCase) || []).join('');
		const date = new Date(Date.now()).toString().slice(4, 15);
	
		element.href = URL.createObjectURL(file)
		element.download = `${tourName}_${date}_${[props.round]}.${fileFormat}`
		element.click()
	}, [csv, props.round, rows]);

	return (
		<section>

			<div className={styles.subheader}>
				<div>All times in {new Date(Date.now()).toString().slice(25)}</div>
				<div className={['button'].join(' ')} onClick={handleDownload}>
					<div><FaDownload /></div>
					<div>Download</div>
				</div>
				<div>
					<div className={styles.searchBar}>
						<FaSearch />
						<AutoComplete
							onChange={(options: Player[]) => setChoices(options)}
							onSubmit={() => Promise.resolve()}
							action=''
							options={Array.from(props.playerDict.values())}
							convert={(p: Player) => getDisplayName(p)}
							styles={styles}
							placeholder={'Search'}
						/>
					</div>
				</div>
			</div>			
			
			<table className='download-table full-width'>
				<thead>
					<tr>
						<td style={{width: '27.5%'}}>White</td>
						<td style={{width: '12.5%'}}>Link</td>
						<td style={{width: '5%'}}></td>
						<td style={{width: '10%'}}>Start Time</td>
						<td style={{width: '5%'}}></td>
						<td style={{width: '12.5%'}}>Watch Live</td>
						<td style={{width: '27.5%'}}>Black</td>
					</tr>
				</thead>
				<tbody>
					{loaded && !pairings.length ? <tr><td colSpan={100}>Draw has not yet taken place!</td></tr> : ''}                       
					{rows}
				</tbody>
			</table>
		</section>

	);

}