import React, { ReactElement, useMemo, useCallback, useReducer, useEffect } from 'react';
import { TournamentStatus, Player, TournamentSettings } from '../interfaces';
import Chartist from 'react-chartist';

interface TableProps {
	status: TournamentStatus
	players: Player[]
	settings: TournamentSettings
	round: number
}

export default function Chart(props: TableProps): ReactElement {

	const columns = useMemo(() => {
		return {
			score: {
				mobile: 'Match Points',
				name: 'MP',
				className: 'locked',
				value: (p: Player, round: number) => p.histories.slice(1, round + 1).reduce((acc, curr) => curr ? acc += curr.match || 0 : acc, 0),
				convert: (v: number, p: Player): string => v.toFixed(1)
			},
			gamePoints: {
				mobile: 'Game Points',
				name: 'GP',
				className: 'locked',
				value: (p: Player, round: number) => p.histories.slice(1, round + 1).reduce((acc, curr) => curr ? acc += curr.game || 0 : acc, 0),
				convert: (v: number): string => v.toFixed(1)
			},
			gameDifference: {
				mobile: 'Game Point Difference',
				name: 'G. +/-',
				className: 'locked',
				value: (p: Player, round: number) => {
					let matchPoints = 0;
					let played = 0;
					p.histories.slice(1, round + 1).forEach((curr) => {
						if (!curr) return;
						matchPoints += curr.game || 0;
						played += matchPoints !== null ? 1 : 0;
					});
					return matchPoints * 2 - played;
				},
				convert: (v: number): string => {
					if (!v) return '';
					if (v > 0) return '+' + v.toFixed(1);
					else return v.toFixed(1)
				},
			},
			matchPercentage: {
				mobile: 'Match Point Percentage',
				name: 'M. %',
				className: 'rating',
				value: (p: Player, round: number) => {
					let gamePoints = 0;
					let played = 0;
					p.histories.slice(1, round + 1).forEach((curr) => {
						if (!curr) return;
						gamePoints += curr.game || 0;
						played += curr.played || 0;
					});
					if (played === 0) return -1;
					return gamePoints * 100 / played;
				},
				convert: (v: number): string => v.toString()
			},
			gamePercentage: {
				mobile: 'Game Point Percentage',
				name: 'G. %',
				className: 'rating',
				value: (p: Player, round: number) => {
					let gamePoints = 0;
					let played = 0;
					p.histories.slice(1, round + 1).forEach((curr) => {
						if (!curr) return;
						gamePoints += curr.game || 0;
						played += curr.played || 0;
					});
					if (played === 0) return -1;
					return gamePoints * 100 / played;
				},
				convert: (v: number): string => v.toString()
			},
			performanceRating: {
				mobile: 'Performance Rating',
				name: 'Perf',
				className: 'rating',
				value: (p: Player) => p.performanceRating,
				convert: (v: number): string => v.toFixed(),
				show: (entry: Player, i: number, s: TournamentSettings) => s.showRatings
			},
			ratingDifference: {
				mobile: 'Performance Rating Difference',
				name: 'P. +/-',
				className: 'rating',
				value: (p: Player): number => {
					if (!p.performanceRating) return 0;
					return p.performanceRating - p.rating;
				},
				convert: (v: number): string => {
					if (!v) return '';
					if (v > 0) return '+' + v.toString();
					else return v.toString()
				},
				show: (entry: Player, i: number, s: TournamentSettings) => s.showRatings
			},
			expectedScore: {
				mobile: 'Expected Score',
				name: 'Expected',
				className: 'rating',
				value: (p: Player): number => p.expectedScore,
				convert: (v: number, p: Player): string => v.toFixed(1) + ' / ' + p.played,
				show: (entry: Player, i: number, s: TournamentSettings) => s.showRatings
			},
			expectedDifference: {
				mobile: 'Score to Expected',
				name: 'E. +/-',
				className: 'rating',
				value: (p: Player): number => p.score - p.expectedScore,
				convert: (v: number): string => {
					if (typeof v === 'undefined') return '';
					if (v > 0) return '+' + v.toFixed(2);
					else return v.toFixed(2)
				},
				show: (entry: Player, i: number, s: TournamentSettings) => s.showRatings
			}
		}
	}, [props.round]);

	const totalRounds = useMemo((): number => {
		let totalRounds: number;
		if (props.status.round > props.settings.totalRounds) totalRounds = props.status.round;
		else if (props.settings.totalRounds === 'Infinity') totalRounds = props.status.round;
		else totalRounds = props.settings.totalRounds;
		return totalRounds;
	}, [props.status.round, props.settings.totalRounds]);
	
	const labels = [] as string[];
	const table = useMemo((): {[key: string]: number[][]} => {
		let res = {} as {[key: string]: number[][]};
		for (let i = 0; i < props.players.length; i++) {
			let player = props.players[i];
			for (let [k, meta] of Object.entries(columns)) {
				if (!res[k]) res[k] = [];
				let row = [];
				for (let j = 1; j < totalRounds + 1; j++) {
					labels[j - 1] = j.toString();
					row.push(meta.value(player, j));
				}
				res[k][i] = row;
			}
		}
		return res;
	}, [props.players]);

	const k = 'gamePoints';
	console.log(table[k]);
		
	return (
		<section>
			<div id='#chart1'>
				<Chartist
					data={{
						labels,
						series: table[k]
					}}
					options={{
						high: 30,
						low: 0,
						fullWidth: true,
						// As this is axis specific we need to tell Chartist to use whole numbers only on the concerned axis
						axisY: {
							onlyInteger: true,
							offset: 20
						}
					}}
					type='Line'
				/>
			</div>
		</section>
	);
}