import React, { ReactElement, useMemo} from 'react';
import { Route, Redirect, Switch, Link } from 'react-router-dom';
import Pairings, { PairingsProps } from './Pairings';
import * as regexes from '../utils/regexes';
import * as icons from '../assets/icons';
import styles from '../css/pairings.module.css';

export default function RoundSwitcher(props: PairingsProps & {section: 'pairings' | 'standings', round: number}): ReactElement {

	let rounds = [];
	let totalRounds = props.settings.totalRounds === 'Infinity' ? 1 : props.settings.totalRounds;
	for (let i = 1; i < totalRounds + 1; i++) {
		rounds.push(
			<Link
				className={['header-button', styles.pairingRound, props.round === i ? styles.roundSelected : '', i > props.status.round ? styles.nullRound : ''].join(' ')}
				to={`/${props.id}/${props.section}/${i}`}
				key={['round', i].join('.')}
			>
				<div className='full-text'><h4>Round {i}</h4></div>
			</Link>
		);
	}

	const streamBox = useMemo(() => {
		if (!props.settings.streamURL) return false;
		let matches = props.settings.streamURL.match(regexes.streamLink);
		if (!matches) return null;
		let [domain, path] = matches.slice(1);
		let site: 'twitch' | 'youtube' = domain.split('.').shift() as 'twitch' | 'youtube';
		const streamDict = {
			twitch: 'https://twitch.tv/',
			youtube: 'https://youtube.com/'
		};
		return (
			<a 
				href={streamDict[site] + path}
				className={['header-button', 'stream-link', site + '-link'].join(' ')}
				target='_blank'
				rel='noopener noreferrer'
			>
				<div><img src={icons[site]} alt={site}/></div>
				<div><h2>Commentary</h2></div>
			</a>
		);
	}, [props.settings.streamURL]);

	return (
		<>
			<section>
				<div className='button-list'>
					<Link to={`/${props.id}/pairings`} className={['header-button', props.section === 'pairings' ? 'selected' : ''].join(' ')}>
						<div className='full-text'><h2>Pairings</h2></div>
					</Link>
					<Link to={`/${props.id}/standings`} className={['header-button', props.section === 'standings' ? 'selected' : ''].join(' ')}>
						<div className='full-text'><h2>Standings</h2></div>
					</Link>
					{streamBox}
				</div>
			</section>
			<section>
				<div className='button-list'>
					{rounds}
				</div>
			</section>
		</>
	)
}